body {
  font-family: 'Georgia', serif;
  color: #333;
  margin: 0;
  padding: 0;
  background-color: #f9f9f9;
  font-size: 14px;
}

header {
  background-color: #ffefd5; 
  color: #333;
  padding: 10px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 2px solid #ffdab9;
}

header .logo img {
  max-height: 40px;
}

h1 {
  text-align: center;
  font-family: 'Comic Sans MS', cursive; 
  font-weight: bold;
  display: inline;
}

nav a {
  margin: 0 10px;
  color: #333;
  text-decoration: none;
  font-weight: bold;
  font-size: 1em;
  transition: color 0.3s ease; 
}

nav a:hover {
  color: #ff6347; 
}

.news-section {
  display: flex;
  flex-direction: column;
  padding: 20px;
  background-color: #fff;
  gap: 20px;
}

.main-news {
  flex: 1;
  position: relative;
  width: 100%;
  max-width: 100%;
}

.main-news img {
  width: 100%;
  height: auto;
  border-radius: 10px;
  transition: transform 0.3s ease; 
}

.main-news img:hover {
  transform: scale(1.05); 
}

.main-news .news-title {
  position: absolute;
  bottom: 10px;
  left: 20px;
  color: white;
  background-color: rgba(0, 0, 0, 0.5);
  padding: 10px;
  border-radius: 5px;
  font-size: 1.8em;
  width: calc(100% - 40px);
  box-sizing: border-box;
}

.side-news {
  display: flex;
  flex-direction: column;
  gap: 15px;
  max-width: 100%;
}

.side-news .news-item {
  display: flex;
  align-items: center;
  background-color: white;
  border-radius: 5px;
  padding: 10px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  transition: box-shadow 0.3s ease; 
}

.side-news .news-item:hover {
  box-shadow: 0 8px 12px rgba(0, 0, 0, 0.2);
}

.side-news .news-item img {
  width: 64px;
  height: 64px;
  border-radius: 5px;
  flex-shrink: 0;
}

.side-news .news-title {
  margin-left: 10px;
  font-size: 0.9em;
  color: #333;
}

.articles-section,
.submit-section {
  text-align: center;
}

footer {
  background-color: #333;
  color: white;
  text-align: center;
  padding: 20px;
  margin-top: 40px;
  font-size: 1.1em;
}

button {
  background-color: #ffefd5;
  border: 2px solid #ffdab9;
  border-radius: 10px;
  padding: 10px 20px;
  font-size: 1.1em;
  color: #333;
  transition: background-color 0.3s ease, transform 0.3s ease;
}

button:hover {
  background-color: #ffdab9;
  transform: scale(1.05);
}

@media (min-width: 769px) {
  .news-section {
      flex-direction: row;
  }

  .main-news {
      flex: 3;
  }

  .side-news {
      flex: 1;
      max-width: 35%;
  }
}

@media (max-width: 768px) {
  .news-section {
      flex-direction: column;
      align-items: center;
  }

  .main-news {
      max-width: 100%;
  }

  .side-news {
      flex-direction: row;
      flex-wrap: wrap;
      gap: 10px;
      max-width: 100%;
  }

  .side-news .news-item {
      flex-basis: 48%;
  }

  .main-news .news-title {
      font-size: 1.5em;
      padding: 8px;
  }
}

@media (max-width: 480px) {
  .news-section {
      padding: 10px;
  }

  .side-news .news-item {
      flex-basis: 100%;
      padding: 10px;
  }

  .main-news img,
  .side-news img {
      width: 100%;
      height: auto;
  }

  nav a {
      font-size: 0.85em;
  }

  .main-news .news-title,
  .side-news .news-title {
      font-size: 1.2em;
  }

  button {
      padding: 10px;
      font-size: 1em;
  }
}
